@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&family=Plus+Jakarta+Sans:wght@300;400;500;600;700;800&family=JetBrains+Mono:wght@300;400;500;600;700&display=swap');
@import './enhanced.css';
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Global Styles */
* {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

*::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Plus Jakarta Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
  min-height: 100vh;
  line-height: 1.6;
  color-scheme: light dark;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Enhanced focus styles for accessibility */
*:focus-visible {
  outline: 2px solid #8b5cf6;
  outline-offset: 2px;
}

/* Skip to content link for accessibility */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: #8b5cf6;
  color: white;
  padding: 8px;
  text-decoration: none;
  border-radius: 4px;
  z-index: 1000;
  transition: top 0.3s;
}

.skip-link:focus {
  top: 6px;
}

@layer components {
  /* Enhanced Modern 3D Button Styles */
  .btn-primary {
    @apply inline-flex items-center justify-center px-6 py-3 text-base font-semibold text-white bg-gradient-to-r from-violet-600 via-purple-700 to-fuchsia-800 rounded-2xl hover:from-violet-700 hover:via-purple-800 hover:to-fuchsia-900 focus:outline-none focus:ring-4 focus:ring-violet-500/50 focus:ring-offset-2 transition-all duration-300 shadow-2xl hover:shadow-3xl hover:scale-105 transform active:scale-95;
    background-image: linear-gradient(135deg, #7c3aed 0%, #8b5cf6 25%, #a855f7 50%, #c084fc 75%, #e879f9 100%);
    box-shadow: 0 10px 25px -5px rgba(124, 58, 237, 0.5), 0 8px 10px -6px rgba(124, 58, 237, 0.3);
  }
  
  .btn-secondary {
    @apply inline-flex items-center justify-center px-6 py-3 text-base font-semibold text-violet-700 bg-white/95 backdrop-blur-sm border-2 border-violet-300 rounded-2xl hover:bg-white hover:border-violet-400 focus:outline-none focus:ring-4 focus:ring-violet-500/25 focus:ring-offset-2 transition-all duration-300 shadow-xl hover:shadow-2xl hover:scale-105 transform active:scale-95;
    box-shadow: 0 8px 20px -5px rgba(139, 92, 246, 0.3), 0 6px 8px -6px rgba(139, 92, 246, 0.2);
  }
  
  .btn-success {
    @apply inline-flex items-center justify-center px-6 py-3 text-base font-semibold text-white bg-gradient-to-r from-emerald-600 via-teal-700 to-cyan-800 rounded-2xl hover:from-emerald-700 hover:via-teal-800 hover:to-cyan-900 focus:outline-none focus:ring-4 focus:ring-emerald-500/50 focus:ring-offset-2 transition-all duration-300 shadow-2xl hover:shadow-3xl hover:scale-105 transform active:scale-95;
    background-image: linear-gradient(135deg, #059669 0%, #0d9488 25%, #0891b2 50%, #06b6d4 75%, #22d3ee 100%);
    box-shadow: 0 10px 25px -5px rgba(5, 150, 105, 0.5), 0 8px 10px -6px rgba(5, 150, 105, 0.3);
  }
  
  .btn-danger {
    @apply inline-flex items-center justify-center px-6 py-3 text-base font-semibold text-white bg-gradient-to-r from-red-600 via-pink-700 to-rose-800 rounded-2xl hover:from-red-700 hover:via-pink-800 hover:to-rose-900 focus:outline-none focus:ring-4 focus:ring-red-500/50 focus:ring-offset-2 transition-all duration-300 shadow-2xl hover:shadow-3xl hover:scale-105 transform active:scale-95;
    background-image: linear-gradient(135deg, #dc2626 0%, #ea580c 25%, #f97316 50%, #fb7185 75%, #f43f5e 100%);
    box-shadow: 0 10px 25px -5px rgba(220, 38, 38, 0.5), 0 8px 10px -6px rgba(220, 38, 38, 0.3);
  }

  .btn-warning {
    @apply inline-flex items-center justify-center px-6 py-3 text-base font-semibold text-white bg-gradient-to-r from-amber-600 via-orange-700 to-red-800 rounded-2xl hover:from-amber-700 hover:via-orange-800 hover:to-red-900 focus:outline-none focus:ring-4 focus:ring-amber-500/50 focus:ring-offset-2 transition-all duration-300 shadow-2xl hover:shadow-3xl hover:scale-105 transform active:scale-95;
    background-image: linear-gradient(135deg, #d97706 0%, #ea580c 25%, #f97316 50%, #fb923c 75%, #fbbf24 100%);
    box-shadow: 0 10px 25px -5px rgba(217, 119, 6, 0.5), 0 8px 10px -6px rgba(217, 119, 6, 0.3);
  }

  /* Enhanced Modern 3D Card Styles */
  .card {
    @apply bg-white/95 backdrop-blur-xl rounded-3xl shadow-2xl border border-white/40 hover:shadow-3xl transition-all duration-300 hover:scale-[1.02] transform hover:-translate-y-2;
    box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25), 0 0 0 1px rgba(255, 255, 255, 0.1);
  }
  
  .card-hover {
    @apply transition-all duration-300 hover:transform hover:-translate-y-3 hover:shadow-3xl hover:scale-[1.03];
  }

  .glass-card {
    @apply bg-white/30 backdrop-blur-xl rounded-3xl shadow-2xl border border-white/50 hover:bg-white/40 transition-all duration-300;
    box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.15), 0 0 0 1px rgba(255, 255, 255, 0.2);
  }
  
  .floating-card {
    @apply bg-white/95 backdrop-blur-xl rounded-3xl shadow-3xl border border-white/50 hover:shadow-4xl transition-all duration-500;
    box-shadow: 0 35px 60px -12px rgba(0, 0, 0, 0.3), 0 0 0 1px rgba(255, 255, 255, 0.3);
  }

  /* Enhanced Interactive 3D Cards */
  .interactive-card {
    @apply card cursor-pointer select-none transition-all duration-300 hover:scale-[1.05] hover:-translate-y-3 active:scale-[0.98];
    box-shadow: 0 20px 40px -12px rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(255, 255, 255, 0.1);
  }

  .feature-card {
    @apply card p-6 text-center hover:scale-105 transition-all duration-500 hover:rotate-1;
    box-shadow: 0 20px 40px -12px rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(255, 255, 255, 0.1);
  }
}

@layer utilities {
  /* Enhanced Modern 3D Animations */
  .animate-fade-in-up {
    animation: fadeInUp 0.8s ease-out;
  }
  
  .animate-fade-in {
    animation: fadeIn 0.6s ease-out;
  }
  
  .animate-slide-in-right {
    animation: slideInRight 0.6s ease-out;
  }
  
  .animate-bounce-in {
    animation: bounceIn 0.8s ease-out;
  }

  .animate-pulse-slow {
    animation: pulse 3s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }

  .animate-float {
    animation: float 6s ease-in-out infinite;
  }

  .animate-glow {
    animation: glow 2s ease-in-out infinite alternate;
  }

  .animate-slide-up {
    animation: slideUp 0.5s ease-out;
  }

  .animate-scale-in {
    animation: scaleIn 0.4s ease-out;
  }

  .animate-rotate-in {
    animation: rotateIn 0.6s ease-out;
  }

  .animate-bounce-gentle {
    animation: bounceGentle 2s ease-in-out infinite;
  }

  .animate-3d-float {
    animation: float3d 4s ease-in-out infinite;
  }

  .animate-3d-rotate {
    animation: rotate3d 8s linear infinite;
  }
  
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(40px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes slideInRight {
    from {
      opacity: 0;
      transform: translateX(40px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  
  @keyframes bounceIn {
    0% {
      opacity: 0;
      transform: scale(0.3);
    }
    50% {
      opacity: 1;
      transform: scale(1.05);
    }
    70% {
      transform: scale(0.9);
    }
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }

  @keyframes float {
    0%, 100% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-10px);
    }
  }

  @keyframes float3d {
    0%, 100% {
      transform: translateY(0px) rotateX(0deg) rotateY(0deg);
    }
    25% {
      transform: translateY(-10px) rotateX(5deg) rotateY(5deg);
    }
    50% {
      transform: translateY(-5px) rotateX(0deg) rotateY(10deg);
    }
    75% {
      transform: translateY(-15px) rotateX(-5deg) rotateY(5deg);
    }
  }

  @keyframes rotate3d {
    0% {
      transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    100% {
      transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
    }
  }

  @keyframes glow {
    from {
      box-shadow: 0 0 20px rgba(99, 102, 241, 0.3);
    }
    to {
      box-shadow: 0 0 30px rgba(99, 102, 241, 0.6);
    }
  }
  
  @keyframes slideUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes scaleIn {
    from {
      opacity: 0;
      transform: scale(0.8);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }
  
  @keyframes rotateIn {
    from {
      opacity: 0;
      transform: rotate(-10deg) scale(0.8);
    }
    to {
      opacity: 1;
      transform: rotate(0deg) scale(1);
    }
  }
  
  @keyframes bounceGentle {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-5px);
    }
  }
  
  .text-shadow {
    text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3), 0 0 20px rgba(99, 102, 241, 0.2);
  }
  
  .luxury-text {
    @apply text-gray-800;
  }
  
  .line-clamp-2 {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  
  .line-clamp-3 {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  
  .scrollbar-hide {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  
  .scrollbar-hide::-webkit-scrollbar {
    display: none;
  }
  
  .scrollbar-thin {
    scrollbar-width: thin;
    scrollbar-color: rgba(156, 163, 175, 0.5) transparent;
  }
  
  .scrollbar-thin::-webkit-scrollbar {
    width: 6px;
  }
  
  .scrollbar-thin::-webkit-scrollbar-track {
    background: transparent;
  }
  
  .scrollbar-thin::-webkit-scrollbar-thumb {
    background-color: rgba(156, 163, 175, 0.5);
    border-radius: 3px;
  }
  
  .scrollbar-thin::-webkit-scrollbar-thumb:hover {
    background-color: rgba(156, 163, 175, 0.7);
  }

  /* Enhanced Modern 3D Gradients */
  .bg-gradient-modern {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  }

  .bg-gradient-sunset {
    background: linear-gradient(135deg, #ff6b6b 0%, #feca57 50%, #48dbfb 100%);
  }

  .bg-gradient-ocean {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
  }

  .bg-gradient-forest {
    background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  }

  .bg-gradient-warm {
    background: linear-gradient(135deg, #ff9a9e 0%, #fecfef 50%, #fecfef 100%);
  }

  /* Enhanced Modern 3D Shadows */
  .shadow-modern {
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  }

  .shadow-glow {
    box-shadow: 0 0 20px rgba(99, 102, 241, 0.3);
  }

  .shadow-3xl {
    box-shadow: 0 35px 60px -12px rgba(0, 0, 0, 0.25);
  }

  .shadow-4xl {
    box-shadow: 0 50px 80px -20px rgba(0, 0, 0, 0.3);
  }

  /* Enhanced 3D Hover Effects */
  .hover-lift {
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }

  .hover-lift:hover {
    transform: translateY(-8px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  }

  .hover-glow:hover {
    box-shadow: 0 0 30px rgba(99, 102, 241, 0.4);
  }

  .hover-3d:hover {
    transform: perspective(1000px) rotateX(5deg) rotateY(5deg) translateZ(20px);
    box-shadow: 0 25px 50px rgba(0, 0, 0, 0.2);
  }

  /* Enhanced 3D Focus States */
  .focus-ring {
    @apply focus:outline-none focus:ring-4 focus:ring-violet-500/50 focus:ring-offset-2;
  }

  /* Enhanced 3D Transitions */
  .transition-smooth {
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }

  .transition-bounce {
    transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }

  .transition-3d {
    transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
}

/* Enhanced Luxury selection */
::selection {
  background: rgba(99, 102, 241, 0.3);
  color: white;
}

/* Enhanced Premium focus styles */
*:focus {
  outline: none;
}

/* Enhanced Luxury text shadow */
.luxury-text-shadow {
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3), 0 0 20px rgba(99, 102, 241, 0.3);
}

/* Enhanced Responsive utilities */
@media (max-width: 640px) {
  .mobile-hidden {
    display: none;
  }
  
  .mobile-full {
    width: 100%;
  }
  
  .mobile-text-sm {
    font-size: 0.875rem;
  }
  
  .mobile-text-xs {
    font-size: 0.75rem;
  }

  .mobile-p-4 {
    padding: 1rem;
  }

  .mobile-text-center {
    text-align: center;
  }
}

@media (max-width: 768px) {
  .tablet-hidden {
    display: none;
  }
  
  .tablet-full {
    width: 100%;
  }
}

@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  
  .animate-float,
  .animate-bounce-gentle,
  .animate-pulse-slow,
  .animate-3d-float,
  .animate-3d-rotate {
    animation: none;
  }
  
  .hover:scale-105,
  .hover:scale-[1.02],
  .hover:-translate-y-1,
  .hover:-translate-y-2,
  .hover-3d:hover {
    transform: none;
  }
}
